import styled from "styled-components";

const Label = styled.label`
  background: ${props => {
    if (props.type === "api") {
      console.log(props.type);
      return "#67C867";
    } else {
      console.log(props.type);
      return "green";
    }
  }};
  font-size: 12px;
  margin-right: 5px;
  margin-top: 5px;
  border-radius: 5px;
  min-width: 55px;
  text-align: center;
  padding: 6px 0;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
`;

export default props => {
  return (
    <Label background={props.type} className="label-tag">
      {props.name}
    </Label>
  );
};
